:host {
  display: block;
  line-height: 1;
}

:host .table td,
:host .table th {
  white-space: pre-wrap;
}

:host .table thead {
  z-index: 1;
}

:host .table.table-sm thead th {
  padding: 0.5rem 0.25rem;
}

:host .table thead th {
  padding: 0.75rem 0.5rem;
}

:host .table thead th.thead-xxs {
  width: 2.3rem;
}

:host .table-sm thead th.thead-xxs {
  padding: 0.25rem;
}

:host .form-check {
  padding: 0;
}

:host .table-sorter {
  cursor: pointer;
}

:host .table-sorter>.cell {
  display: inline-block;
  height: 100%;
  position: relative;
  width: 100%;
  line-height: 1.1;
}

:host .table-sorter>.cell:before {
  border-top: 5px solid #000;
  bottom: .15rem;
}

:host .table-sorter>.cell:after {
  border-bottom: 5px solid #000;
  border-top: 5px solid transparent;
  top: -.2rem;
}

:host .table-sorter>.cell:after,
:host .table-sorter>.cell:before {
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  right: 5px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  opacity: .3;
}

:host .table-sorter.asc>.cell:after,
:host .table-sorter.desc>.cell:before {
  opacity: .7;
}

:host .table-sorter.asc>.cell:before,
:host .table-sorter.desc>.cell:after {
  opacity: 0;
}

:host table {
  table-layout: fixed;
}

:host .no-records-found {
  height: 30vh;
}

:host .fixed-table-header th {
  position: sticky;
  top: 0;
}

:host .fixed-table-header thead {
  position: sticky;
  top: 0;
  background: white;
}

:host .fixed-table-header thead tr {
  border-top-width: 0;
}

:host .table-description {
  font-size: 0.5rem;
  font-weight: normal !important;
}

:host .table-top,
:host .table-bottom {
  padding: 0.5rem;
}

:host .table-top label {
  margin-bottom: 0;
}

:host .table-selectedInfo,
:host .table-pageSizeMenu,
:host .table-pageInfo {
  float: left;
  color: #7b809a;
  font-size: .875rem;
}

:host .table-selectedInfo,
:host .table-pageInfo {
  margin: 0.7rem 0;
  padding-right: 2rem;
}

:host .table-search,
:host .table-pagination {
  float: right;
}

:host .table .form-check>.form-check-input[type=radio] {
  background-color: #fff;
  cursor: pointer;
}

:host .table-bottom:after,
:host .table-top:after {
  clear: both;
  content: " ";
  display: table;
}

:host .table-search input {
  font-size: .875rem;
  color: #495057;
  border: 1px solid #f0f2f5;
  border-radius: 0.375rem;
  padding: 6px 12px;
}

:host .table-top .table-pageSizeMenu label select {
  border-color: #f0f2f5;
  border-radius: 0.375rem;
  padding: 6px;
}

:host .tr.table-sorter {
  cursor: pointer;
}

:host tr storm-toolbar storm-button .btn {
  width: 1.5rem;
  height: 1.5rem;
  padding: 0.3rem 0.3rem;
}

:host tr:hover storm-toolbar.storm-dynamic-label storm-button .btn:not(.btn-icon-only) {
  width: auto;
  height: auto;
  padding: .06rem .5rem .06rem .2rem;
}

:host tr storm-toolbar.storm-dynamic-label storm-button .storm-button-label {
  display: none;
}

:host tr:hover storm-toolbar.storm-dynamic-label storm-button .storm-button-label {
  display: block;
}