import { Component, Element, Event, EventEmitter, forceUpdate, h, Host, Listen, Method, Prop, Watch } from '@stencil/core';
import { FnSearchInput } from '../../utils/functional/functional';
import { SharedStyles } from '../../utils/shared-styles';
import {
  arrayMove,
  createFunctionFromString,
  debounce,
  generateUid,
  getFieldValue,
  isModuleFunctionString,
  parseClassNames,
  resolvePropertyValue,
  setFieldValue,
} from '../../utils/utils';
import type { RowMoveInterface, TableColumnInterface, TableRenderOption } from './interface';
import type {
  CheckboxGroupControlOptionInterface,
  CheckControlOptionInterface,
  ControlOptionInterface,
  DropdownTreeControlOptionInterface,
  TextAreaControlOptionInterface,
} from './storm-table-cell/interface';

@Component({
  tag: 'storm-table',
  styleUrl: 'storm-table.css',
  shadow: true,
})
export class StormTable {
  private _pageDate: object[];
  private _cellCheckChangeTimeoutRafId: Record<string, NodeJS.Timeout> = {};

  private _bodyRef: HTMLStormTableBodyElement;
  private _selectedInfoRef: HTMLDivElement;
  private readonly _breakpoints = [
    { size: '4xs', width: 239 },
    { size: '3xs', width: 359 },
    { size: 'xxs', width: 479 },
    { size: 'xs', width: 576 },
    { size: 'sm', width: 768 },
    { size: 'md', width: 992 },
    { size: 'lg', width: 1200 },
    { size: 'xl', width: 1400 },
    { size: 'xxl', width: 1600 },
    { size: '3xl', width: 1920 },
    { size: '4xl', width: Infinity },
  ];
  @Element() host: HTMLElement;
  @Prop() uid: string = generateUid('storm-table');
  @Prop({ mutable: true }) fieldId: string | number = 'id';
  @Prop({ mutable: true }) fieldSelected: string = '__selected';
  @Prop() fieldIndex: string = '__index';

  @Prop() pagination: boolean = false;
  @Prop() sidePagination: 'razor' | 'stencil' = 'stencil';
  @Prop() searchable: boolean = false;
  @Prop() pageSizeMenu: boolean = true;
  @Prop() pageInfo: boolean = true;
  @Prop() selectedInfo: boolean = true;
  @Prop() escape: boolean = true;
  @Prop({ mutable: true }) classes: Record<string, boolean> | string;

  @Prop() cellTemplate: string = '{value}';

  @Prop() clickRowTo?: 'select' | 'check';
  @Prop() selection: 'single' | 'multiple' | 'none' = 'single';
  @Prop() pageList: Array<number> = [5, 10, 15, 20, 25];
  @Prop() tableHeight?: string;

  @Prop({ mutable: true, reflect: false }) columns: TableColumnInterface[];
  @Prop({ mutable: true, reflect: false }) dataSet: object[];
  @Prop({ mutable: true }) pageNumber: number = 1;
  @Prop({ mutable: true }) pageSize: number = 10;
  @Prop({ mutable: true }) searchText?: string;
  @Prop({ mutable: true }) sortName?: string | number;
  @Prop({ mutable: true }) sortOrder?: 'asc' | 'desc';
  @Prop({ mutable: true }) groupBy?: string;

  @Prop({ mutable: true }) totalRows: number;
  @Prop({ mutable: true }) totalSelected: number;
  @Prop({ mutable: true, reflect: false }) totalChecked: any;

  @Prop({ mutable: true, reflect: false }) isColumnResponsive: boolean = false;

  @Prop({ reflect: false }) expand: { visibleColumnCount: number } = { visibleColumnCount: 0 };

  @Event() pageNumberChange: EventEmitter<number>;
  @Event() pageSizeChange: EventEmitter<number>;
  @Event() sortChange: EventEmitter<{ sortName?: string | number; sortOrder?: 'asc' | 'desc' }>;
  @Event() searchChanged: EventEmitter<string>;
  @Event() tableWillRender: EventEmitter<TableRenderOption>;
  @Event() rowMove: EventEmitter<RowMoveInterface>;

  @Event() rowWillSelect: EventEmitter<string>;
  @Event() rowWillUnselect: EventEmitter<string>;
  @Event() rowSelect: EventEmitter<string>;
  @Event() rowUnselect: EventEmitter<string>;
  @Event() rowDidSelect: EventEmitter<object>;
  @Event() rowDidUnselect: EventEmitter<object>;
  @Event() rowSelectChange: EventEmitter<object>;
  @Event() selectWillChange: EventEmitter;
  @Event() selectChange: EventEmitter<number>;
  @Event() selectDidChange: EventEmitter<number>;

  @Event() cellWillCheck: EventEmitter<{ id: string; field: string | number }>;
  @Event() cellWillUncheck: EventEmitter<{ id: string; field: string | number }>;
  @Event() cellCheck: EventEmitter<{ id: string; field: string | number }>;
  @Event() cellUncheck: EventEmitter<{ id: string; field: string | number }>;
  @Event() cellDidCheck: EventEmitter<{ row: object; column: TableColumnInterface }>;
  @Event() cellDidUncheck: EventEmitter<{ row: object; column: TableColumnInterface }>;
  @Event() cellCheckChange: EventEmitter<{ row: object; column: TableColumnInterface }>;

  @Event() checkWillChange: EventEmitter<string | number>;
  @Event() checkChange: EventEmitter<{ total: number; field: string | number }>;
  @Event() checkDidChange: EventEmitter<{ total: number; column: TableColumnInterface }>;

  @Event() clickRow: EventEmitter<string>;

  @Watch('dataSet')
  watchDataSetHandler() {
    this.initDataSet();
    this.rerender();
  }

  @Watch('columns')
  async watchColumnsHandler() {
    this.initColumns();
  }

  @Watch('totalSelected')
  watchTotalSelectedHandler(_newValue: number, _oldValue: number) {
    this.debouncedDisplaySelectedCount();
  }

  @Listen('searchChanged')
  searchChangedHandler(ev: CustomEvent<string>) {
    if (this.sidePagination === 'stencil') {
      this.searchText = ev.detail;
    } else {
      this.tableWillRender.emit({
        pageNumber: this.pageNumber,
        pageSize: this.pageSize,
        sortName: this.sortName,
        sortOrder: this.sortOrder,
        searchText: ev.detail,
      });
    }
  }

  @Listen('pageNumberChange')
  pageNumberChangeHandler(ev: CustomEvent<number>) {
    if (this.sidePagination === 'stencil') {
      this.pageNumber = ev.detail;
    } else {
      this.tableWillRender.emit({ pageNumber: ev.detail, pageSize: this.pageSize, sortName: this.sortName, sortOrder: this.sortOrder, searchText: this.searchText });
    }
  }

  @Listen('pageSizeChange')
  pageSizeChangeHandler(ev: CustomEvent<number>) {
    if (this.sidePagination === 'stencil') {
      this.pageSize = ev.detail;
    } else {
      this.tableWillRender.emit({ pageNumber: this.pageNumber, pageSize: ev.detail, sortName: this.sortName, sortOrder: this.sortOrder, searchText: this.searchText });
    }
  }

  @Listen('sortChange')
  sortChangeHandler(ev: CustomEvent<{ sortName?: string | number; sortOrder?: 'asc' | 'desc' }>) {
    if (this.sidePagination === 'stencil') {
      this.sortName = ev.detail.sortName;
      this.sortOrder = ev.detail.sortOrder;
    } else {
      this.tableWillRender.emit({
        pageNumber: this.pageNumber,
        pageSize: this.pageSize,
        sortName: ev.detail.sortName,
        sortOrder: ev.detail.sortOrder,
        searchText: this.searchText,
      });
    }
  }

  @Listen('rowWillSelect')
  async rowWillSelectHandler(ev: CustomEvent<string>) {
    if (this.sidePagination === 'stencil') {
      if (this.selection === 'single') {
        await this.unselectAll();
      }
      this.rowSelect.emit(ev.detail);
    }
  }

  @Listen('rowSelect')
  async rowSelectHandler(ev: CustomEvent<string>) {
    const row = await this.getRowById(ev.detail);
    setFieldValue(row, this.fieldSelected, true);
    this._bodyRef.updateRowSelection(row);
    this.rowDidSelect.emit(row);
  }

  @Listen('rowDidSelect')
  rowDidSelectHandler(ev: CustomEvent<object>) {
    this.rowSelectChange.emit(ev.detail);
  }

  @Listen('rowWillUnselect')
  rowWillUnselectHandler(ev: CustomEvent<string>) {
    if (this.sidePagination === 'stencil') {
      this.rowUnselect.emit(ev.detail);
    }
  }

  @Listen('rowUnselect')
  async rowUnselectHandler(ev: CustomEvent<string>) {
    const row = await this.getRowById(ev.detail);
    setFieldValue(row, this.fieldSelected, false);
    this._bodyRef.updateRowSelection(row);
    this.rowDidSelect.emit(row);
  }

  @Listen('rowDidUnselect')
  rowDidUnselectHandler(ev: CustomEvent<object>) {
    this.rowSelectChange.emit(ev.detail);
  }

  @Listen('rowSelectChange')
  rowSelectChangeHandler(_: CustomEvent<object>) {
    this.debouncedRowSelectionUpdate();
  }

  @Listen('selectWillChange')
  async selectWillChangeHandler(_: CustomEvent) {
    if (this.sidePagination === 'stencil') {
      const selected = await this.getSelected();
      this.selectChange.emit(selected.length);
    }
  }

  @Listen('selectChange')
  selectChangeHandler(ev: CustomEvent<number>) {
    this.totalSelected = ev.detail;
    this.debouncedDisplaySelectedCount();
    this.selectDidChange.emit(ev.detail);
  }

  @Listen('cellWillCheck')
  async cellWillCheckHandler(ev: CustomEvent<{ id: string; field: string | number }>) {
    if (this.sidePagination === 'stencil') {
      const col = await this.getColumnByField(ev.detail.field);
      if (col.type === 'radio') {
        await this.uncheckAll(col.field);
      }
      this.cellCheck.emit(ev.detail);
    }
  }

  @Listen('cellCheck')
  async cellCheckHandler(ev: CustomEvent<{ id: string; field: string | number }>) {
    const col = await this.getColumnByField(ev.detail.field);
    const row = await this.getRowById(ev.detail.id);
    setFieldValue(row, col.field, true);
    this._bodyRef.updateRowCheckbox(row, col);
    this.cellDidCheck.emit({ row: row, column: col });
  }

  @Listen('cellDidCheck')
  cellDidCheckHandler(ev: CustomEvent<{ row: object; column: TableColumnInterface }>) {
    this.cellCheckChange.emit(ev.detail);
  }

  @Listen('cellWillUncheck')
  cellWillUncheckHandler(ev: CustomEvent<{ id: string; field: string | number }>) {
    if (this.sidePagination === 'stencil') {
      this.cellUncheck.emit(ev.detail);
    }
  }

  @Listen('cellUncheck')
  async cellUncheckHandler(ev: CustomEvent<{ id: string; field: string | number }>) {
    const col = await this.getColumnByField(ev.detail.field);
    const row = await this.getRowById(ev.detail.id);
    setFieldValue(row, col.field, false);
    this._bodyRef.updateRowCheckbox(row, col);
    this.cellDidCheck.emit({ row: row, column: col });
  }

  @Listen('cellDidUncheck')
  cellDidUncheckHandler(ev: CustomEvent<{ row: object; column: TableColumnInterface }>) {
    this.cellCheckChange.emit(ev.detail);
  }

  @Listen('cellCheckChange')
  cellCheckChangeHandler(ev: CustomEvent<{ row: object; column: TableColumnInterface }>) {
    const col = ev.detail.column;
    const rafId = this._cellCheckChangeTimeoutRafId[col.field];

    if (rafId) {
      clearTimeout(rafId);
      delete this._cellCheckChangeTimeoutRafId[col.field];
    }

    this._cellCheckChangeTimeoutRafId[col.field] = setTimeout(() => {
      delete this._cellCheckChangeTimeoutRafId[col.field];
      this.checkWillChange.emit(col.field);
    }, 100);
  }

  @Listen('checkWillChange')
  async checkWillChangeHandler(ev: CustomEvent<string | number>) {
    if (this.sidePagination === 'stencil') {
      const checked = await this.getChecked(ev.detail);
      this.checkChange.emit({ total: checked.length, field: ev.detail });
    }
  }

  @Listen('checkChange')
  async checkChangeHandler(ev: CustomEvent<{ total: number; field: string | number }>) {
    this.setTotalChecked(ev.detail.field, ev.detail.total);
    if (ev.detail.field === this.fieldSelected) {
      this.totalSelected = ev.detail.total;
    }
    const col = await this.getColumnByField(ev.detail.field);
    await this._bodyRef.updateColumnCheckbox(col);
    this.checkDidChange.emit({ total: ev.detail.total, column: col });
  }

  @Listen('clickCell')
  async clickCellHandler(ev: CustomEvent<{ id: string; field: string | number }>) {
    const col = await this.getColumnByField(ev.detail.field);
    if (col.type === 'checkbox' || col.type === 'radio') {
      await this.checkClick(ev.detail.id, ev.detail.field);
    } else {
      this.clickRow.emit(ev.detail.id);
    }
  }

  @Listen('clickRow')
  async clickRowHandler(ev: CustomEvent<string>) {
    if (this.clickRowTo === 'check') {
      await this.checkClick(ev.detail, this.fieldSelected);
    } else if (this.clickRowTo === 'select') {
      const row = await this.getRowById(ev.detail);
      getFieldValue(row, this.fieldSelected) ? this.unselect(ev.detail) : this.select(ev.detail);
    }
  }

  @Method()
  async moveRow(from: number, to: number) {
    arrayMove(this.dataSet, from, to);
    this.dataSet.forEach((row, index) => {
      const rowIndex = getFieldValue(row, this.fieldIndex);
      if (rowIndex !== index) {
        this.rowMove.emit({
          row: row,
          from: rowIndex as number,
          to: index,
        });
        setFieldValue(row, this.fieldIndex, index);
      }
    });
    this.rerender();
  }

  @Method()
  async dispatchCustomEvent(type: string, detail?: object): Promise<void> {
    this[type].emit(detail);
  }

  @Method()
  async getRowAt(index: number): Promise<object> {
    return this.dataSet.find(row => getFieldValue(row, this.fieldIndex) === index);
  }

  @Method()
  async getRowById(id: string | object): Promise<object> {
    if (typeof id === 'object') return id;
    return this.dataSet.find(row => getFieldValue(row, this.fieldId) === id);
  }

  @Method()
  async getColumnByField(field: string | number | TableColumnInterface): Promise<TableColumnInterface> {
    if (typeof field === 'object') return field;
    return this.columns.find(col => col.field === field);
  }

  @Method()
  async getSelected(): Promise<object[]> {
    if (this.sidePagination === 'stencil') {
      return this.dataSet.filter(row => getFieldValue(row, this.fieldSelected));
    }
    return null;
  }

  @Method()
  async getChecked(field: string | number): Promise<object[]> {
    if (this.sidePagination === 'stencil') {
      const col = await this.getColumnByField(field);
      return this.dataSet.filter(row => getFieldValue(row, col.field) !== null && getFieldValue(row, col.field));
    }
    return null;
  }

  @Method()
  async select(id: string) {
    this.rowWillSelect.emit(id);
  }

  @Method()
  async selectAll() {
    this.dataSet
      .filter(row => !getFieldValue(row, this.fieldSelected))
      .forEach(row => {
        this.select(getFieldValue(row, this.fieldId) as string);
      });
  }

  @Method()
  async unselect(id: string) {
    this.rowWillUnselect.emit(id);
  }

  @Method()
  async unselectAll() {
    this.dataSet
      .filter(row => getFieldValue(row, this.fieldSelected))
      .forEach(row => {
        this.unselect(getFieldValue(row, this.fieldId) as string);
      });
  }

  @Method()
  async check(id: string, field: string | number) {
    const row = await this.getRowById(id);
    if (getFieldValue(row, field)) return;
    this.cellWillCheck.emit({ id: id, field: field });
  }

  @Method()
  async uncheck(id: string, field: string | number) {
    const row = await this.getRowById(id);
    if (!getFieldValue(row, field)) return;
    this.cellWillUncheck.emit({ id: id, field: field });
  }

  @Method()
  async checkAll(field: string | number) {
    const col = await this.getColumnByField(field);
    const rowsToCheck = this.dataSet.filter(row => !getFieldValue(row, field));
    
    for (const row of rowsToCheck) {
      const option = await this.resolveColumnData<CheckControlOptionInterface>(col, [row, getFieldValue(row, this.fieldIndex), col]);
      if (option && option.disabled) continue;

      this.check(getFieldValue(row, this.fieldId) as string, field);
    }
  }

  @Method()
  async uncheckAll(field: string | number) {
    const col = await this.getColumnByField(field);
    const rowsToUncheck = this.dataSet.filter(row => getFieldValue(row, field));
    
    for (const row of rowsToUncheck) {
      const option = await this.resolveColumnData<CheckControlOptionInterface>(col, [row, getFieldValue(row, this.fieldIndex), col]);
      if (option && option.disabled) continue;

      this.uncheck(getFieldValue(row, this.fieldId) as string, field);
    }
  }

  private async resolveColumnData<T = unknown>(col: TableColumnInterface | null | undefined, args: unknown[]): Promise<T | undefined> {
    const result = resolvePropertyValue(col, 'data', args);
    return result instanceof Promise ? await result : result;
  }

  private readonly rerender = debounce(async () => {
    forceUpdate(this);
  }, 100);

  private readonly debouncedDisplaySelectedCount = debounce(async () => {
    if (!this.isSelectedInfoVisible() || !this._selectedInfoRef) return;
    this._selectedInfoRef.innerHTML = this.totalSelected ? `已選取 ${this.totalSelected} 筆` : '';
  }, 100);

  private readonly debouncedRowSelectionUpdate = debounce(async () => {
    this.selectWillChange.emit();
  }, 100);

  @Method()
  async checkClick(id: string, field: string | number | TableColumnInterface) {
    const col = await this.getColumnByField(field);
    const row = await this.getRowById(id);
    if (getFieldValue(row, col.field)) {
      if (col.type === 'radio' && (!col.data || !(col.data as CheckControlOptionInterface).unchecked)) return;
      this.uncheck(id, col.field);
    } else this.check(id, col.field);
  }

  isPaginationVisible(): boolean {
    return this.columns && this.pageInfo && this.pagination && this._pageDate && this._pageDate.length > 0;
  }

  isPageSizeMenuVisible(): boolean {
    return this.pageSizeMenu && this.isPaginationVisible();
  }

  isSelectedInfoVisible(): boolean {
    return this.selectedInfo && this.clickRowTo != null;
  }

  getTotalChecked(field: string | number): number {
    if (!this.totalChecked) return 0;
    return this.totalChecked[field];
  }

  handleSearchChanged(ev: CustomEvent<string>) {
    ev.stopPropagation();
    const value = ev.detail?.trim();
    if (this.searchText === value) return;
    this.searchChanged.emit(value);
  }

  handleColumnCheckChange(ev: Event, col: TableColumnInterface) {
    const checked = ev.target['checked'];
    if (checked) this.checkAll(col.field);
    else this.uncheckAll(col.field);
  }

  handleColumnClick(col: TableColumnInterface) {
    if (!col.sortable) return;
    if (col.field == this.sortName) {
      if (this.sortOrder == 'desc') this.sortChange.emit({ sortName: undefined, sortOrder: undefined });
      else this.sortChange.emit({ sortName: this.sortName, sortOrder: 'desc' });
    } else this.sortChange.emit({ sortName: col.field, sortOrder: 'asc' });
  }

  renderPagination() {
    if (!this.isPaginationVisible()) return;
    return (
      <div class="table-pagination">
        <storm-pagination
          page-number={this.pageNumber}
          total-pages={Math.ceil(this.totalRows / this.pageSize)}
          onPageChange={ev => {
            this.pageNumberChange.emit(ev.detail);
          }}
        ></storm-pagination>
      </div>
    );
  }

  renderPageSizeMenu() {
    if (!this.isPageSizeMenuVisible()) return;
    return (
      <div class="table-pageSizeMenu">
        <label>
          顯示{' '}
          <select class="table-selector" onChange={ev => this.pageSizeChange.emit(Number((ev.currentTarget as HTMLInputElement).value))}>
            {this.pageList.map(page => {
              return (
                <option value={page} selected={this.pageSize == page}>
                  {page}
                </option>
              );
            })}
          </select>{' '}
          筆結果
        </label>
      </div>
    );
  }

  renderSearch() {
    if (!this.searchable) return;
    return (
      <div class="table-search">
        <FnSearchInput placeholder="請輸入關鍵字" onValueChanged={ev => this.handleSearchChanged(ev)} />
      </div>
    );
  }

  renderPageInfo() {
    if (!this.pageInfo) return;
    return (
      <div class="table-pageInfo">
        {this.searchable && this.searchText ? `篩選 ${this._pageDate.length} 筆，` : ''}
        {this.pagination && this._pageDate.length > 0
          ? `顯示第 ${(this.pageNumber - 1) * this.pageSize + 1} 至 ${this.totalRows < this.pageNumber * this.pageSize ? this.totalRows : this.pageNumber * this.pageSize} 筆，`
          : ''}
        共 {this.totalRows} 筆
      </div>
    );
  }

  renderSelectedInfo() {
    if (!this.isSelectedInfoVisible()) return;
    return <div ref={elm => (this._selectedInfoRef = elm)} class="table-selectedInfo"></div>;
  }

  renderTableTop() {
    if (!this.isPageSizeMenuVisible() && !this.searchable) return;
    return (
      <div class="table-top p-1">
        {this.renderPageSizeMenu()}
        {this.renderSearch()}
      </div>
    );
  }

  renderTableBottom() {
    if (!this.isSelectedInfoVisible() && !this.isPaginationVisible()) return;
    return (
      <div class="table-bottom p-1">
        {this.renderPageInfo()}
        {this.renderSelectedInfo()}
        {this.renderPagination()}
      </div>
    );
  }

  private initDataSet(): void {
    if (!this.dataSet?.length || !this.columns?.length) return;

    const checkboxRadioColumns = this.columns.filter(column => column.type === 'checkbox' || column.type === 'radio');
    const checkboxRadioColumnsLength = checkboxRadioColumns.length;

    // 使用單次遍歷處理所有邏輯
    let selectedCount = 0;
    const columnCheckedCounts = new Map<string | number, number>();

    // 初始化計數器
    for (let i = 0; i < checkboxRadioColumnsLength; i++) {
      columnCheckedCounts.set(checkboxRadioColumns[i].field, 0);
    }

    const dataSetLength = this.dataSet.length;
    const isStencilPagination = this.sidePagination === 'stencil';
    
    for (let index = 0; index < dataSetLength; index++) {
      const row = this.dataSet[index];
      
      // 初始化必要欄位 - 批量檢查以減少函數調用
      if (getFieldValue(row, this.fieldId) == null) {
        setFieldValue(row, this.fieldId, generateUid('row'));
      }
      if (getFieldValue(row, this.fieldIndex) == null) {
        setFieldValue(row, this.fieldIndex, index);
      }
      if (getFieldValue(row, this.fieldSelected) == null) {
        setFieldValue(row, this.fieldSelected, false);
      }

      // 處理 checkbox/radio 欄位並同時計數
      for (let i = 0; i < checkboxRadioColumnsLength; i++) {
        const column = checkboxRadioColumns[i];
        const fieldValue = getFieldValue(row, column.field);
        
        if (fieldValue == null) {
          setFieldValue(row, column.field, false);
        } else if (isStencilPagination && fieldValue) {
          columnCheckedCounts.set(column.field, columnCheckedCounts.get(column.field)! + 1);
        }
      }

      // 計算選中的行數
      if (isStencilPagination && getFieldValue(row, this.fieldSelected)) {
        selectedCount++;
      }
    }

    // 更新統計數據
    if (isStencilPagination) {
      this.totalSelected = selectedCount;
      this.totalRows = dataSetLength;

      columnCheckedCounts.forEach((count, field) => {
        this.setTotalChecked(field, count);
      });
    }
  }


  private initColumns(): void {
    if (!this.columns) return;

    this.columns.forEach((col, index) => {
      if (col.type == null) {
        col.type = 'text';
      } else if (typeof col.type === 'string' && isModuleFunctionString(col.type)) {
        col.type = createFunctionFromString(col.type);
      }

      if (col.type !== 'action') {
        col.field ??= ['checkbox', 'radio'].includes(col.type as string) ? generateUid(col.type as string, index.toString()) : index;
        col.searchable ??= this.searchable;
      }

      const classes = col.classes ?? {};
      const responsiveClassPairs = Object.entries(classes).filter(
        ([className, value]) => className.startsWith('d-table-') && className !== 'd-table-none' && className !== 'd-table-cell' && value === true,
      );

      if (responsiveClassPairs.length > 0) {
        col.responsive = true;
        col.rwdClassReversal = {};

        const hasNone = responsiveClassPairs.some(([cls]) => cls.endsWith('-none'));
        const hasCell = responsiveClassPairs.some(([cls]) => cls.endsWith('-cell'));

        if (hasNone && hasCell) {
          col.rwdClassReversal['d-table-none'] = classes['d-table-none'] !== true;
        } else if (hasNone && !hasCell) {
          col.rwdClassReversal['d-table-none'] = classes['d-table-none'] !== true;
        }
        for (const [className] of responsiveClassPairs) {
          if (className.endsWith('-cell')) {
            const noneClass = className.replace(/-cell$/, '-none');
            col.rwdClassReversal[noneClass] = true;
          } else if (className.endsWith('-none')) {
            const cellClass = className.replace(/-none$/, '-cell');
            col.rwdClassReversal[cellClass] = true;
          }
        }
      }

      if (typeof col.data === 'string') {
        if (isModuleFunctionString(col.data)) {
          col.data = createFunctionFromString(col.data);
        } else if (col.data.startsWith('{') && col.data.endsWith('}')) {
          try {
            col.data = JSON.parse(col.data) as
              | ControlOptionInterface
              | CheckControlOptionInterface
              | DropdownTreeControlOptionInterface
              | TextAreaControlOptionInterface
              | CheckboxGroupControlOptionInterface;
          } catch (error) {
            console.warn('無法解析欄位 data JSON：', col.data);
          }
        }
      }
    });

    if (this.columns.some(col => col.responsive === true)) {
      this.isColumnResponsive = true;
      this.columns.unshift({
        type: 'detail',
      } as TableColumnInterface);
    }

    this.expand.visibleColumnCount = this.columns.filter(column => column.visible !== false).length;

    if (this.clickRowTo === 'check') {
      const checkColumn = this.columns.find(col => col.type === 'checkbox' || col.type === 'radio');
      if (checkColumn && (!this.fieldSelected || this.fieldSelected === '__selected')) {
        this.fieldSelected = checkColumn.field?.toString();
      }
    }

    if (!this.dataSet) {
      this.tableWillRender.emit({
        pageNumber: this.pageNumber,
        pageSize: this.pageSize,
        sortName: this.sortName,
        sortOrder: this.sortOrder,
        searchText: this.searchText,
      });
    } else {
      this.initDataSet();
    }
  }

  private _resizeObserver?: ResizeObserver;
  private responsiveResize() {
    if (this._resizeObserver) return;

    this._resizeObserver = new ResizeObserver(() => {
      this.refreshColumnVisibility();
    });

    this._resizeObserver.observe(this.host);
  }

  private _breakpointIndex?: number;
  private readonly refreshColumnVisibility = debounce(() => {
    const tableElem = this.host.shadowRoot?.querySelector('table');
    if (!tableElem) return;

    const tableWidth = tableElem.offsetWidth;

    if (this._breakpointIndex != null) {
      const currentBreakpoint = this._breakpoints[this._breakpointIndex];
      const prevBreakpoint = this._breakpoints[this._breakpointIndex - 1];

      if (tableWidth >= currentBreakpoint.width && (!prevBreakpoint || tableWidth < prevBreakpoint.width)) {
        return;
      }
    }

    let newBreakpointIndex: number | undefined;
    const breakpointsLength = this._breakpoints.length;

    for (let index = 0; index < breakpointsLength; index++) {
      const { size, width: bpWidth } = this._breakpoints[index];
      const isLast = index === breakpointsLength - 1;
      const isMatch = tableWidth >= bpWidth && (isLast || tableWidth < this._breakpoints[index + 1].width);

      tableElem.classList.toggle(`storm-table-${size}`, isMatch);

      if (isMatch) {
        newBreakpointIndex = index;
      }
    }

    this._breakpointIndex = newBreakpointIndex;

    const visibleColumns: any[] = [];
    let hasResponsiveHidden = false;

    for (const col of this.columns) {
      const isVisible = this.isColumnVisible(col);
      col['__columnVisible'] = isVisible;

      if (isVisible) {
        visibleColumns.push(col);
      } else if (col.responsive === true) {
        hasResponsiveHidden = true;
      }
    }

    const elems = this._bodyRef?.querySelectorAll('.detail-toggle, .row-detail');

    if (hasResponsiveHidden) {
      this.expand.visibleColumnCount = visibleColumns.length;
      elems?.forEach(elem => {
        elem.classList.toggle('d-table-none', false);
      });
    } else {
      this.expand.visibleColumnCount = visibleColumns.filter(col => col.type !== 'detail').length;
      elems?.forEach(elem => {
        elem.classList.toggle('d-table-none', true);
      });
    }

    const colspanValue = this.expand.visibleColumnCount.toString();
    this._bodyRef?.querySelectorAll('.full-cell[colspan]').forEach(elem => {
      elem.setAttribute('colspan', colspanValue);
    });
  }, 100);

  private isColumnVisible(column: TableColumnInterface): boolean {
    if (column.visible === false) return false;
    if (!column.classes || column.responsive !== true) return true;

    let isVisible = true;

    if (column.classes['d-table-none'] === true) isVisible = false;
    if (column.classes['d-table-cell'] === true) isVisible = true;

    if (typeof this._breakpointIndex === 'number' && Array.isArray(this._breakpoints)) {
      for (let i = 0; i <= this._breakpointIndex; i++) {
        const size = this._breakpoints[i]?.size;
        if (!size) continue;

        if (column.classes[`d-table-${size}-none`] === true) isVisible = false;
        if (column.classes[`d-table-${size}-cell`] === true) isVisible = true;
      }
    }

    return isVisible;
  }

  setTotalChecked(field: string | number, total: number) {
    this.totalChecked ??= {};
    this.totalChecked[field] = total;
  }

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');

    this.initColumns();
  }

  disconnectedCallback() {
    // 清理定時器
    Object.values(this._cellCheckChangeTimeoutRafId).forEach(id => {
      if (id) clearTimeout(id);
    });
    this._cellCheckChangeTimeoutRafId = {};
    
    // 清理 ResizeObserver
    if (this._resizeObserver) {
      this._resizeObserver.disconnect();
      this._resizeObserver = undefined;
    }
    
    // 清理緩存
    this._searchableColumns = null;
    this._lastSearchText = undefined;
    this._lastSortField = undefined;
    this._lastSortOrder = undefined;
    this._lastGroupBy = undefined;
  }

  private _searchableColumns: TableColumnInterface[] | null = null;
  private _lastSearchText: string | undefined;
  private _lastSortField: string | number | undefined;
  private _lastSortOrder: 'asc' | 'desc' | undefined;
  private _lastGroupBy: string | undefined;

  componentWillRender() {
    if (!this.dataSet || !this.columns) return;
    
    this.classes = parseClassNames(this.classes);
    this._pageDate = this.dataSet;
    
    if (this.sidePagination === 'stencil') {
      // 搜尋優化 - 只在搜尋條件改變時重新處理
      if (this.searchable && this.searchText) {
        const searchText = this.searchText.toLowerCase();
        
        if (!this._searchableColumns || this._lastSearchText !== this.searchText) {
          this._searchableColumns = this.columns.filter(col => col.searchable);
        }
        
        if (this._lastSearchText !== this.searchText) {
          this._pageDate = this._pageDate.filter(row => {
            for (let i = 0; i < this._searchableColumns!.length; i++) {
              const col = this._searchableColumns![i];
              const value = getFieldValue(row, col.field);
              if ((value + '').toLowerCase().includes(searchText)) {
                return true;
              }
            }
            return false;
          });
          this._lastSearchText = this.searchText;
        }
      } else if (this._lastSearchText !== this.searchText) {
        this._lastSearchText = this.searchText;
      }

      // 排序優化 - 只在排序條件改變時重新排序
      const sortField = this.sortName || this.fieldIndex;
      const needsReSort = this._lastSortField !== sortField || 
                         this._lastSortOrder !== this.sortOrder || 
                         this._lastGroupBy !== this.groupBy;
      
      if (sortField && needsReSort) {
        const isDesc = this.sortOrder === 'desc';
        const hasGroupBy = !!this.groupBy;
        
        this._pageDate.sort((a, b) => {
          const aSort = getFieldValue(a, sortField);
          const bSort = getFieldValue(b, sortField);

          if (hasGroupBy) {
            const aGroup = getFieldValue(a, this.groupBy!) as string;
            const bGroup = getFieldValue(b, this.groupBy!) as string;
            if (aSort == bSort) {
              return aGroup.localeCompare(bGroup);
            }
          }

          if (this.sortName) {
            const result = (aSort + '').localeCompare(bSort + '');
            return isDesc ? -result : result;
          } else {
            return (aSort as number) - (bSort as number);
          }
        });
        
        this._lastSortField = sortField;
        this._lastSortOrder = this.sortOrder;
        this._lastGroupBy = this.groupBy;
      }
    }
  }

  render() {
    if (!this._pageDate) return;
    let data = this._pageDate;
    if (this.pagination && this.sidePagination == 'stencil') {
      const from = (this.pageNumber - 1) * this.pageSize + 1;
      const to = this.pageNumber * this.pageSize;
      data = data.slice(from - 1, to);
    }

    return (
      <Host>
        {this.renderTableTop()}
        <div class={{ 'text-sm': true, 'table-responsive': true, border: this.classes && !!this.classes['table-bordered'] }}>
          <div class={{ 'table-container': true, 'fixed-table-header': !!this.tableHeight }} style={{ height: !!this.tableHeight ? this.tableHeight : 'inherit' }}>
            <storm-table-body ref={elm => (this._bodyRef = elm)} table={this} dataSet={data} timeStamp={Date.now()}></storm-table-body>
          </div>
        </div>
        {this.renderTableBottom()}
      </Host>
    );
  }

  componentDidRender() {
    this.debouncedDisplaySelectedCount();
    if (this.isColumnResponsive) {
      this.responsiveResize();
    }
  }
}
